/* ====================================== key   ====================================== */
import React from 'react';
import connect from '@redux/connect';
// =====================================================================
class RouterRefresh extends React.Component {
	refresh(){
		//  redux 
		// 
		this.props.$action.doType('do_refresh');
	}
	goSelf(){
		this.props.$action.getCode('views/react/router/router-refresh.js');
		this.props.history.push('/code')
	}
	goMain(){
		this.props.$action.getCode('views/react/main.js');
		this.props.history.push('/code')
	}
	render(){
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}></button>
					<button className="btn-my-default mr20" onClick={this.goMain.bind(this)}>main.js </button>
				</div>
				
				<div className="lh30 mvvm-code samebox mt10">
		        	<dl className="mvvm-list">
			        	<dt>: </dt>
			        	<dd><b>&lt;Route component=Router key=this.props.state.My.key /&gt;</b></dd>
			        	<dd><b>this.props.$action.doType('do_refresh')</b> redux </dd>
			        </dl>
		        </div>
		        
				<div className="samebox mt10">
					<button className="btn-my-o mr20" onClick={this.refresh.bind(this)}></button>
					{this.props.state.key}
				</div>
			</div>
		)
	}
}

export default connect(RouterRefresh)



