/* ======================================   ====================================== */
import React from 'react';
import connect from '@redux/connect';

// =====================================================================
class BindProperty extends React.Component{
	goSelf(){
		this.props.$action.getCode('views/react/data-bind/property.js');
		this.props.history.push('/code')
	}
	render(){
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}></button>
				</div>
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
		        		<dt>HTML :</dt>
		        		<dd><b>const property = href:'',target:'',title:''</b></dd>
		        		<dd><b>&lt;div ...property&gt;</b></dd>
			        </dl>
					<dl className="mvvm-list samebox mt10">
		        		<dt> HTML :</dt>
		        		<dd><b>&lt;div x='123'&gt;</b><s x='123'></s></dd>
		        		<dd><b>&lt;div data-x=false&gt;</b><s data-x={false}>data- </s></dd>
		        		<dd><b>&lt;div aria-hidden=false&gt;</b><s aria-hidden={true}>aria-hidden </s></dd>
			        </dl>
			        <dl className="mvvm-list samebox">
		        		<dt>data- HTML  HTML React  data- :</dt>
		        		<dd><b>&lt;div data-xx=false&gt;</b><s data-xx={false}>data- </s></dd>
		        		<dd><b>&lt;div data-custom-attribute="foo"&gt;</b><s data-custom-attribute="foo">data- </s></dd>
			        </dl>
			        <dl className="mvvm-list samebox">
		        		<dt>aria-hidden=true</dt>
		        		<dd></dd>
		        		<dd>, </dd>
		        		<dd><b>&lt;div aria-hidden=true&gt;</b><s aria-hidden={true}></s></dd>
			        </dl>
				</div>
			</div>
		)
	}
}

export default connect(BindProperty)



