/* ======================================  --- render  React Elements tree  ====================================== */
import React from 'react';
import connect from '@redux/connect';
// ===================================================================== Inheritance InversionII
const ii = (WrappedComponent) => {
	return class II extends WrappedComponent {
		state = { value: ''}
		render() {
			//  render  React Elements tree 
			const elementsTree = super.render();
			let newProps = {};
			if (elementsTree && elementsTree.type === 'input') {
				newProps = {
					value: this.state.value,
					placeholder: '',
					onChange: (event)=>{ this.setState({ value: event.target.value}) }
				}
			}
			const props = Object.assign({}, elementsTree.props, newProps)
			const newElementsTree = React.cloneElement(elementsTree, props, elementsTree.props.children)
			return newElementsTree
		}
	}
}
// ===================================================================== WrappedComponent
class C extends React.Component{
	render(){
		return <input />
	}
}

const IComponent = ii(C);
// =====================================================================
class HigherIIChange extends React.Component{
	state = { }
	goSelf(){
		this.props.$action.getCode('views/react/component-higher/ii/higher-ii-change.js');
		this.props.history.push('/code')
	}
	render(){
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}>--- render  React Elements tree </button>
				</div>
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
		        		<dt>- render  React Elements tree </dt>
		        		<dd><b>const elementsTree = super.render()</b> elementsTree </dd>
		        		<dd><b>elementsTree.type</b> html </dd>
			        </dl>
			        <dl className="mvvm-list samebox mt10">
		        		<dt>React.cloneElement(ReactElement,props(object),children(ReactElement))</dt>
		        		<dd> ReactElement  propsrefkey props</dd>
		        		<dd><b>ReactElement</b>   <b>const elementsTree = super.render()</b></dd>
		        		<dd><b>newProps</b> props,  </dd>
		        		<dd><b>children</b> <b>elementsTree.props.children</b>   <b>&lt;div&gt;</b> html </dd>
		        		<dt>React.cloneElement(elementsTree=super.render(), props={}, elementsTree.props.children)</dt>
		        		<dt>React.cloneElement(elementsTree=super.render(), props={}, &lt;div&gt;html&lt;/div&gt;)</dt>
			        </dl>
			        <dl className="mvvm-list samebox">
		        		<dt></dt>
		        		<dd>input   value  onChange , </dd>
			        </dl>
				</div>
				
				<IComponent/>
			</div>
		)
	}
}

export default connect(HigherIIChange)


