/* ======================================  Route  ====================================== */
import React from 'react';
import { Route, Link } from 'react-router-dom';
import connect from '@redux/connect';
// ===================================================================== Route
const MyRoute = ({cpt:Component, path})=>(
	<Route path={path} render={ props => (
		<Component {...props} />
	)} />
)
const MyComponent = () => (<div className="samebox" style={{'fontSize':'30px'}}> Route </div>)
// =====================================================================
class RouterMyRoute extends React.Component {
	goSelf(){
		this.props.$action.getCode('views/react/router/router-my-route.js');
		this.props.history.push('/code')
	}
	render(){
		let { match } = this.props;
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}> Route</button>
				</div>
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
		        		<dt> Route: </dt>
		        		<dd><b>cpt:Component</b></dd>
		        		<dd><b>&lt;Component ...props /&gt;</b> Component  cpt cptprops </dd>
			        </dl>
				</div>
				<div className="samebox">
					<Link to={`${match.url}/my-route`} className="btn-my-o mr20"> Route</Link>
				</div>
				<MyRoute path={`${match.url}/my-route`} cpt={MyComponent} />
			</div>
		)
	}
}

export default connect(RouterMyRoute)



