/* ======================================  html  ====================================== */
import React from 'react';
import connect from '@redux/connect';
// =====================================================================
class BindHtml extends React.Component{
	goSelf(){
		this.props.$action.getCode('views/react/data-bind/html.js');
		this.props.history.push('/code')
	}
	//  html
	insertHtml(){
		 return {__html: '<b style="color:blue"> html </b>'};
	}
	render(){
		const innerHtml = {__html:'<b style="color:blue"> html </b>'}
		const htmlArr = [
			<h1 key="h1"></h1>,
			<h2 key="h2"></h2>,
			<h2 key="h3"> key="" ,</h2>
		]
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}> html</button>
				</div>
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
		        		<dt>dangerouslySetInnerHTML = :</dt>
		        		<dd><b>insertHtml()return __html:'&lt;b&gt;html&lt;/b&gt;'</b></dd>
			        	<dd><b>&lt;div dangerouslySetInnerHTML=this.insertHtml()&gt;</b> html</dd>
			        </dl>
			        <div className="samebox" dangerouslySetInnerHTML={this.insertHtml()}></div>
			        
			        <dl className="mvvm-list samebox mt10">
		        		<dt>dangerouslySetInnerHTML =  innerHtml :</dt>
		        		<dd><b>const innerHtml = __html:'&lt;b&gt;html&lt;/b&gt;'</b>  html </dd>
			        	<dd><b>&lt;div dangerouslySetInnerHTML= innerHtml &gt;</b> html</dd>
			        </dl>
			        <div className="samebox" dangerouslySetInnerHTML={innerHtml}></div>
			        
			        <dl className="mvvm-list samebox mt10">
		        		<dt>dangerouslySetInnerHTML = __html:'':</dt>
			        	<dd><b>&lt;div dangerouslySetInnerHTML=__html:'&lt;b&gt;&lt;/b&gt;'&gt;</b> html</dd>
			        </dl>
			        <div className="samebox" dangerouslySetInnerHTML={{__html:'<b style="color:blue"> html </b>'}}></div>
				
					<dl className="mvvm-list samebox mt10">
		        		<dt> jsx  html</dt>
			        	<dd><b>&lt;div&gt; html &lt;/div&gt;</b>	{<s> jsx  html</s>}</dd>
			        	<dd><b>(&lt;div&gt; html &lt;/div&gt;)</b>{(<s> jsx  html</s>)}</dd>
			        	<dd><b> htmlArr </b> htmlArr=[&lt;div key="mykey"&gt; html &lt;/div&gt;,&lt;div key="mykey"&gt; html &lt;/div&gt;]</dd>
			        </dl>
			        <div className="samebox">{ htmlArr }</div>
				</div>
			</div>
			
		)
	}
}

export default connect(BindHtml)



