/* ======================================  redux   ====================================== */
import React from 'react';
import connect from '@redux/connect';
// =====================================================================
class BindBase extends React.Component{
	componentDidMount(){
		console.log(this.props)
	}
	goSelf(){
		this.props.$action.getCode('views/react/react-redux/redux-base.js');
		this.props.history.push('/code')
	}
	render(){
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}>redux </button>
				</div>
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
		        		<dt>redux :</dt>
		        		<dd></dd>
		        	</dl>
				</div>
				
				<div className="samebox mt10">
					<button className="btn-my-o mr20" onClick={()=>{this.props.$action.prompt('...')}}></button>
				</div>
				
				<div className="samebox mt10">
					<button className="btn-my-o mr20" onClick={()=>{this.props.$action.dispatch('do_add')}}></button>
					<button className="btn-my-o mr20" onClick={()=>{this.props.$action.doType('do_minus')}}></button>
					{this.props.state.Test.num}
				</div>
			</div>
		)
	}
}

export default connect(BindBase)



