/* ======================================  state   ====================================== */
import React from 'react';
import connect from '@redux/connect';
// =====================================================================
class StateBase extends React.Component{
	static defaultProps = {
		a:' props '
	}
	//  state
	constructor(props){
		super(props);
		this.state = { id:0, name:'' }
		this.staticVar = ''
		this.staticFn = ()=>{ return ''; }
	}
	//state = { id:0 }	//  state
	componentWillMount(){
		//this.state.id = '';
	}
	componentDidMount(){
		this.setState({name:'this.setState() '});
	}
	goSelf(){
		this.props.$action.getCode('views/react/state/state-base.js');
		this.props.history.push('/code')
	}
	render(){
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}>state </button>
				</div>
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
		        		<dt>state(): state state  DOM</dt>
		        		<dd><b>this.setState(id:1)</b> state </dd>
		        		<dd><b>this.state.id = 1 </b> <b>componentWillMount</b>  state </dd>
		        	</dl>
		        	<dl className="mvvm-list samebox">
		        		<dt>props:()</dt>
		        		<dd><b>static defaultProps = a:0, b:1</b> props </dd>
		        		<dd><b>this.props.a</b>props </dd>
		        	</dl>
				</div>
				
				<div className="samebox mt10 lh30" style={{color:'blue'}}>
					<div>{this.state.name}</div>
					<div>{this.staticVar}</div>
					<div>{this.staticFn()}</div>
				</div>
				
				<div className="samebox mt10" style={{color:'blue'}}>{this.props.a}</div>
			</div>
		)
	}
}

export default connect(StateBase)



