import React from 'react';
// =====================================================================
const link = {
	href:'https://reactjs.org/docs/installation.html',
	target:'_blank'
}
const link2 = {
	href:'https://doc.react-china.org/docs/hello-world.html',
	target:'_blank'
}
const link3 = {
	href:'http://www.runoob.com/js/js-tutorial.html',
	target:'_blank'
}
const Index = () => (
	<div>
		<div className="samebox">
			<a className="btn-my-default mr20" {...link}>react </a>
			<a className="btn-my-default mr20" {...link2}>react </a>
			<a className="btn-my-default mr20" {...link3}>js/html </a>
		</div>
		<dl className="mvvm-list samebox mt10">
			<dt></dt>
			<dd><b>npm install -g create-react-app</b> # </dd>
			<dd><b>create-react-app my-app</b> # </dd>
			<dd><b>cd my-app</b> # </dd>
			<dd><b>npm start</b> #  react</dd>
			<dd><b>npm run build</b> # </dd>
			<dd><b>npm run eject</b> # </dd>
		</dl>
		<dl className="mvvm-list samebox mt10">
			<dt></dt>
			<dd><b>npm install -g create-react-app yarn</b> # </dd>
			<dd><b>create-react-app my-app</b> # </dd>
			<dd><b>cd my-app</b> # </dd>
			<dd><b>yarn start</b> #  react</dd>
			<dd><b>yarn build</b> # </dd>
			<dd><b>yarn eject</b> # </dd>
		</dl>
		<dl className="mvvm-list samebox mt10">
			<dt></dt>
			<dd><b>yarn add react-app-rewired --only=dev</b> #  create-react-app </dd>
			<dd> package.json  scripts   <b>react-scripts</b> <b>react-app-rewired</b> # react-scripts</dd>
			<dd> <b>config-overrides.js</b> # </dd>
		</dl>
		<dl className="mvvm-list samebox mt10">
			<dt></dt>
			<dd><b>yarn add babel-plugin-import --only=dev</b> #  babel </dd>
		</dl>
		<dl className="mvvm-list samebox mt10">
			<dt> antd less </dt>
			<dd><b>yarn add react-app-rewire-less --only=dev</b> #  less</dd>
		</dl>
		<dl className="mvvm-list samebox mt10">
			<dt>antd </dt>
			<dd><b>antd/lib/style/core/base.less</b> # </dd>
		</dl>
		<dl className="mvvm-list samebox mt10">
			<dt> sourceMap</dt>
			<dd><b>react-scripts/config/webpack.config.prod.js</b> #  <b>shouldUseSourceMap = false</b></dd>
		</dl>
		<dl className="mvvm-list samebox mt10">
			<dt></dt>
			<dd><b>"homepage":"./"</b> #  package.json  homepage</dd>
			<dd><b>"theme": "primary-color": "#6699cc"</b> #  package.json </dd>
		</dl>
	</div>
)

export default Index



