/* ======================================  - -  props ====================================== */
import React from 'react';
import connect from '@redux/connect';
// ===================================================================== Props Proxy PP
const pp = (WrappedComponent) => {
	return class PP extends React.Component {
		proc(a) {
			alert(a)
		}
		render() {
			const propertyProps = { 
				a : 0, 
				b : 1
			}
			const methodProps = {
				m:()=>{alert('')}, 								// this.m()
				n: this.proc.bind(this,'') 	// this.props.n()
			}
			//  props
			const contactProps = Object.assign({}, propertyProps, methodProps)
			
			return <WrappedComponent {...contactProps} />
		}
	}
}
// ===================================================================== WrappedComponent
class A extends React.Component{
	render(){
		return (
			<div className="samebox mt10">
				<button className="btn-my-o mr20" onClick={this.props.m.bind(this)}></button>
				<button className="btn-my-o mr20" onClick={this.props.n}></button>
			</div>
		)
	}
}
// ===================================================================== 
const PComponent = pp(A);
// =====================================================================
class HigherPPProps extends React.Component{
	goSelf(){
		this.props.$action.getCode('views/react/component-higher/pp/higher-pp-props.js');
		this.props.history.push('/code')
	}
	render(){
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}>- -  props</button>
				</div>
				
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
						<dt> props</dt>
					</dl>
				</div>
				
				<PComponent/>
			</div>
		)
	}
}

export default connect(HigherPPProps)


