/* ======================================  -  ====================================== */
import React from 'react';
//import { withRouter } from 'react-router-dom';
import connect from '@redux/connect';
// ===================================================================== Inheritance Inversion II
/*
	const ii = (WrappedComponent) => {
		return class II extends WrappedComponent {
			render() {
				return super.render()
			}
		}
	}
*/
// =====================================================================
class HigherII extends React.Component{
	goSelf(){
		this.props.$action.getCode('views/react/component-higher/ii/higher-ii-base.js');
		this.props.history.push('/code')
	}
	render(){
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}>-</button>
				</div>
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
		        		<dt>Inheritance Inversion II</dt>
		        		<dd>, <b>II</b>  <b>WrappedComponent</b></dd>
		        		<dd> this  WrappedComponent statepropscomponent lifecyclerender</dd>
			        </dl>
			        <dl className="mvvm-list samebox mt10">
		        		<dt></dt>
		        		<dd>Render Highjacking</dd>
		        		<dd> state</dd>
			        </dl>
			        <dl className="mvvm-list samebox mt10">
		        		<dt>Render Highjacking</dt>
		        		<dt>  <b>WrappedComponent.render()</b> </dt>
		        		<dt> WrappedComponent , </dt>
		        		<dd>  React Element   props</dd>
		        		<dd> React Elements tree WrappedComponent  children</dd>
		        		<dd></dd>
		        		<dd></dd>
			        </dl>
			        <dl className="mvvm-list samebox mt10">
		        		<dt></dt>
		        		<dd> props  WrappedComponent  React  props render / props</dd>
		        		<dd> WrappedComponent  WrappedComponent  Function  React Element</dd>
			        	<dd> Props Proxy </dd>
			        </dl>
				</div>
			</div>
		)
	}
}

export default connect(HigherII)


