import React from 'react';
import { Route, NavLink, Link } from 'react-router-dom';
import connect from '@redux/connect';
import Nav from  '@js/nav';
// ===================================================================== 
import Router from './router';
import logo from '@images/logo.png';
// =====================================================================
class ReactIndex extends React.Component {
	state = { top:0 }
	//  DO
	componentWillMount(){
		// 
		//this.props.history.listen((location)=>{ console.log(location);})
		//this.context.router.history.listen((location)=>{ console.log(location) })
	}
	//  DOM
	componentDidMount(){
		Nav();
	}
	// 
	componentWillUpdate(nextProps, nextState){
		
	}
	// 
	componentDidUpdate(nextProps, nextState){
		
	}
	// 
	componentWillReceiveProps(nextProps){
		//console.log('',this.props.location.pathname);
	    //console.log('',nextProps.location.pathname);
	}
	// 
	shouldComponentUpdate(nextProps, nextState){
		return true
	}
	// 
	componentWillUnmount(){
		//this.setState = (state,callback)=>{ return;}
	}
	render(){
		return [
			<header className="header" key="header">
				<h1 className="logo"><Link to="/react"><img src={logo} alt=""/></Link></h1>
				<i className="font-navicon small-nav" id="smallNav"></i>
			</header>,
			<section className="container" key="container">
				<aside className="navigation oys" id="navigation">
					<ul>
						<li>
							<h3 className="js-tit"><span>{this.top}</span><i className="font-angle-right"></i></h3>
							<menu>
								<li><NavLink to={{pathname:'/react/router/router-props/200',search:'id=100',hash:'#myHash',state:{name:'react'}}} activeClassName="active">props</NavLink></li>
								<li><NavLink to="/react/router/router-mode" activeClassName="active"></NavLink></li>
								<li><NavLink to="/react/router/router-base" activeClassName="active"></NavLink></li>
								<li><NavLink to="/react/router/router-child" activeClassName="active"></NavLink></li>
								<li><NavLink to="/react/router/router-async" activeClassName="active"></NavLink></li>
								<li>
									<h5 className="js-tit"><span></span><i className="font-angle-right"></i></h5>
									<nav>
										<NavLink to="/react/router/router-map-config" activeClassName="active">map </NavLink>
										<NavLink to="/react/router/router-component-config" activeClassName="active"></NavLink>
									</nav>
								</li>
								<li><NavLink to="/react/router/router-many-component" activeClassName="active"></NavLink></li>
								<li><NavLink to="/react/router/router-prompt" activeClassName="active">Prompt </NavLink></li>
								<li>
									<h5 className="js-tit"><span></span><i className="font-angle-right"></i></h5>
									<nav>
										<NavLink to="/react/router/router-link-nav" activeClassName="active"></NavLink>
										<NavLink to="/react/router/router-order-nav" activeClassName="active"></NavLink>
										<NavLink to="/react/router/router-my-link-nav" activeClassName="active"></NavLink>
									</nav>
								</li>
								<li><NavLink to="/react/router/router-listen" activeClassName="active"></NavLink></li>
								<li><NavLink to="/react/router/router-refresh" activeClassName="active">key </NavLink></li>
								<li><NavLink to="/react/router/router-my-route" activeClassName="active"> Route</NavLink></li>
								<li><NavLink to="/react/router/router-static" activeClassName="active"></NavLink></li>
							</menu>
						</li>
						<li>
							<h3 className="js-tit"><span>state </span><i className="font-angle-right"></i></h3>
							<menu>
								<li><NavLink to="/react/state/state-base" activeClassName="active">state</NavLink></li>
							</menu>
						</li>
						<li>
							<h3 className="js-tit"><span></span><i className="font-angle-right"></i></h3>
							<menu>
								<li><NavLink to="/react/data-bind/data-bind-base" activeClassName="active"></NavLink></li>
								<li><NavLink to="/react/data-bind/data-bind-html" activeClassName="active">html</NavLink></li>
								<li><NavLink to="/react/data-bind/data-bind-property" activeClassName="active"></NavLink></li>
								<li><NavLink to="/react/data-bind/data-bind-style" activeClassName="active"></NavLink></li>
								<li>
									<h5 className="js-tit"><span></span><i className="font-angle-right"></i></h5>
									<nav>
										<NavLink to="/react/data-bind/data-bind-for-array" activeClassName="active"></NavLink>
										<NavLink to="/react/data-bind/data-bind-for-object" activeClassName="active"></NavLink>
									</nav>
								</li>
								<li>
									<h5 className="js-tit"><span></span><i className="font-angle-right"></i></h5>
									<nav>
										<NavLink to="/react/data-bind/if-html" activeClassName="active"></NavLink>
										<NavLink to="/react/data-bind/if-method" activeClassName="active"></NavLink>
										<NavLink to="/react/data-bind/if-render" activeClassName="active">render</NavLink>
										<NavLink to="/react/data-bind/hidden" activeClassName="active">hidden</NavLink>
									</nav>
								</li>
								<li><NavLink to="/react/data-bind/data-bind-event" activeClassName="active"></NavLink></li>
							</menu>
						</li>
						<li>
							<h3 className="js-tit"><span></span><i className="font-angle-right"></i></h3>
							<menu>
								<li><NavLink to="/react/component/props" activeClassName="active">props</NavLink></li>
								<li><NavLink to="/react/component/ref" activeClassName="active">ref</NavLink></li>
								<li><NavLink to="/react/component/life" activeClassName="active"></NavLink></li>
								<li><NavLink to="/react/component/create" activeClassName="active"></NavLink></li>
								<li><NavLink to="/react/component/prop-types" activeClassName="active"></NavLink></li>
								<li>
									<h5 className="js-tit"><span></span><i className="font-angle-right"></i></h5>
									<nav>
										<NavLink to="/react/component/child-get-parent" activeClassName="active"></NavLink>
										<NavLink to="/react/component/parent-get-child" activeClassName="active"></NavLink>
										<NavLink to="/react/component/brother" activeClassName="active"></NavLink>
										<NavLink to="/react/component/eventProxy-brother" activeClassName="active"></NavLink>
									</nav>
								</li>
								<li><NavLink to="/react/component/context" activeClassName="active">context </NavLink></li>
							</menu>
						</li>
						<li>
							<h3 className="js-tit"><span></span><i className="font-angle-right"></i></h3>
							<menu>
								<li><NavLink to="/react/component-higher/higher-base" activeClassName="active"></NavLink></li>
								<li>
									<h5 className="js-tit"><span></span><i className="font-angle-right"></i></h5>
									<nav>
										<NavLink to="/react/component-higher/higher-pp-base" activeClassName="active"></NavLink>
										<NavLink to="/react/component-higher/higher-pp-props" activeClassName="active"> props</NavLink>
										<NavLink to="/react/component-higher/higher-pp-ref" activeClassName="active"> refs </NavLink>
										<NavLink to="/react/component-higher/higher-pp-state" activeClassName="active"> state</NavLink>
										<NavLink to="/react/component-higher/higher-pp-element" activeClassName="active"> elements </NavLink>
									</nav>
								</li>
								<li>
									<h5 className="js-tit"><span></span><i className="font-angle-right"></i></h5>
									<nav>
										<NavLink to="/react/component-higher/higher-ii-base" activeClassName="active"></NavLink>
										<NavLink to="/react/component-higher/higher-ii-condition" activeClassName="active">-</NavLink>
										<NavLink to="/react/component-higher/higher-ii-change" activeClassName="active">- element tree</NavLink>
										<NavLink to="/react/component-higher/higher-ii-state" activeClassName="active">state</NavLink>
										<NavLink to="/react/component-higher/higher-ii-life" activeClassName="active"></NavLink>
									</nav>
								</li>
							</menu>
						</li>
						<li>
							<h3 className="js-tit"><span></span><i className="font-angle-right"></i></h3>
							<menu>
								<li><NavLink to="/react/form/form-base" activeClassName="active"></NavLink></li>
							</menu>
						</li>
						<li>
							<h3 className="js-tit"><span>react-redux</span><i className="font-angle-right"></i></h3>
							<menu>
								<li><NavLink to="/react/react-redux/redux-base" activeClassName="active">redux </NavLink></li>
							</menu>
						</li>
						<li>
							<h3 className="js-tit"><span>html5 </span><i className="font-angle-right"></i></h3>
							<menu>
								<li><NavLink to="/react/database/localStorage" activeClassName="active">localStorage</NavLink></li>
								<li><NavLink to="/react/database/sessionStorage" activeClassName="active">sessionStorage</NavLink></li>
								<li><NavLink to="/react/database/indexdb" activeClassName="active">indexdb</NavLink></li>
							</menu>
						</li>
					</ul>
				</aside>
				<main className="oxys" id="main">
					<section className="content">
						{/* ng router-outletvue router-view */}
						{/* <Router {...this.props} key={this.props.state.My.key} /> */}
						{/* <Route component={Router} {...this.props} key={this.props.state.My.key} /> */}
						<Route component={Router} {...this.props} key={this.props.state.key} />
					</section>
				</main>
			</section>
		]
	}
}

export default connect(ReactIndex)



