/* ======================================  -- ====================================== */
import React from 'react';
import connect from '@redux/connect';
// ===================================================================== Inheritance InversionII
const ii = (WrappedComponent) => {
	return class II extends WrappedComponent {
		componentDidMount(){
			super.componentWillMount(); //  WrappedComponent
		}
		render() {
			return super.render()
		}
	}
}
// ===================================================================== WrappedComponent
class A extends React.Component{
	componentWillMount(){
		console.log('')
	}
	render(){
		return <div className="samebox mt10"></div>
	}
}

const IComponent = ii(A);
// =====================================================================
class HigherIILife extends React.Component{
	constructor(props){
		super(props);
		this.state = { }
	}
	goSelf(){
		this.props.$action.getCode('views/react/component-higher/ii/higher-ii-life.js');
		this.props.history.push('/code')
	}
	render(){
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}>--</button>
				</div>
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
		        		<dt></dt>
			        </dl>
				</div>
				
				<IComponent />
			</div>
		)
	}
}

export default connect(HigherIILife)


