/* ======================================    ====================================== */
import React from 'react';
import connect from '@redux/connect';
// =====================================================================
class Life extends React.Component{
	//  DOM
	componentWillMount(){
		console.log(' DOM')
	}
	//  DOM
	componentDidMount(){
		console.log(' DOM')
	}
	//  DOM
	componentWillUnmount(){
		console.log(' DOM')
	}
	// 
	componentWillUpdate(nextProps, nextState){
		console.log('')
	}
	// 
	componentDidUpdate(nextProps, nextState){
		console.log('')
	}
	// 
	componentWillReceiveProps(nextProps){
		console.log('')
	}
	// truerender
	// renderVirtual DOM
	// falseReactrenderVirtual DOM rendershort circuit
	shouldComponentUpdate(nextProps, nextState){
		console.log('')
		return true
	}
	goSelf(){
		this.props.$action.getCode('views/react/component/life.js');
		this.props.history.push('/code')
	}
	render(){
		return (
			<div>
				<div className="samebox">
					<button className="btn-my-default mr20" onClick={this.goSelf.bind(this)}></button>
				</div>
				<div className="lh30 mvvm-code mt10">
					<dl className="mvvm-list samebox">
		        		<dt><b>componentWillMount()</b>:  DOM</dt>
		        		<dd>renderstate</dd>
			        </dl>
			        
			        <dl className="mvvm-list samebox mt10">
		        		<dt><b>componentDidMount()</b>:  DOM</dt>
		        		<dd>renderDOM</dd>
		        		<dd> <b>this.getDOMNode()</b> WebDOM</dd>
			        </dl>
			        
			        <dl className="mvvm-list samebox mt10">
		        		<dt><b>componentWillUpdate(nextProps, nextState)</b>: </dt>
		        		<dd>componentWillMountpropsstatestateprops</dd>
			        </dl>
			        
			        <dl className="mvvm-list samebox mt10">
		        		<dt><b>componentDidUpdate(nextProps, nextState)</b>: </dt>
		        		<dd>componentDidMountDOM</dd>
			        </dl>
			        
			        <dl className="mvvm-list samebox mt10">
		        		<dt><b>componentWillReceiveProps(nextProps)</b>: </dt>
		        		<dd>props</dd>
		        		<dd>props(statepropsstate</dd>
			        </dl>
			        
			        <dl className="mvvm-list samebox mt10">
		        		<dt><b>shouldComponentUpdate(nextProps, nextState)</b>: </dt>
		        		<dd>propsstaterender</dd>
			        </dl>
			        
			        <dl className="mvvm-list samebox mt10">
		        		<dt><b>componentWillUnmount()</b>: </dt>
		        		<dd>componentDidMount</dd>
			        </dl>
			        
			        <dl className="mvvm-list samebox">
		        		<dt><b>render()</b>:</dt>
		        		<dd>DOMrender</dd>
		        		<dd>1 <b>this.props</b>  <b>this.state</b> </dd>
		        		<dd>2 <b>null</b><b>false</b><b>React</b></dd>
		        		<dd>3</dd>
		        		<dd>4DOM</dd>
			        </dl>
				</div>
			</div>
			
		)
	}
}

export default connect(Life)



